@import "../../globale.scss";

.topbar {
  background-color: white;
  color: $maincolor;
  width: 100%;
  height: 70px;
  position: fixed;
  top: 0;
  z-index: 3;
  box-shadow: 0px 0px 10px -5px black;
  

  .wrapper {
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: all 1s ease-in;


    .left {
      display: flex;
      align-items: center;
      .logo {
        font-size: 40px;
        font-weight: 700;
        text-decoration: none;
        color: inherit;
        margin-right: 40px;
      }
      .itemContainer {
        display: flex;
        align-items: center;
        margin-left: 30px;

        @include mobile {
          display:  none;
        }

        .icon {
          font-size: 18px;
          margin-right: 5px;
        }

        span {
          font-size: 15px;
          font-weight: 700;
        }

        a{
          text-decoration: none;
          color: inherit;
        }
      }
    }

    .right {
      .hamburger {
        font-size: 40px;
        width: 32px;
        height: 25px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        cursor: pointer;
        span {
          width: 100%;
          height: 3px;
          background-color: $maincolor;
          transform-origin: left;
          transition: all 1.5s ease;
        }
      }
    }
  }

  &.active {
    background-color: $maincolor;
    color: white;

    .hamburger {
      span {
        &:first-child {
          background-color: white;
          transform: rotate(45deg);
        }
        &:nth-child(2) {
          display: block;
        }
        &:last-child {
          background-color: white;
          transform: rotate(-45deg);
        }
      }
    }
  }
}
